* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.app {
  background-image: url(./assets/01dclearsky.webp);
  backgound-size: cover;
  background-position: bottom;
  transition: 0.5 ease-out;
}

main {
  min-height: 100vh;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.75)
  );
  padding: 25px;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(212, 121, 88, 0.5);
  border-radius: 16px;
  margin-top: 25px;
  box-shadow: 0px 5px rgba(73, 39, 26, 0.2);

  color: #313131;
  font-size: 20px;

  transition: 0.4s ease-in;
}

.search-box .search-bar:focus {
  background-color: rgba(222, 110, 70, 0.75);
}

.location-box .location {
  color: white;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 50, 0.5);
}

.location-box .date {
  color: white;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 50, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px;
  background-color: rgba(195, 61, 12, 0.2);
  border-radius: 16px;

  padding: 10px 20px;

  color: white;
  font-size: 102px;
  font-weight: 900;
  text-shadow: 4px 4px rgba(195, 61, 12, 0.75);
  text-align: center;
  box-shadow: 3px 6px rgba(195, 61, 12, 0.5);
}

.extended {

display: flex;
justify-content: center;
  align-items: center;
  padding: 25px;
}

